.app-contaner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 3rem 0.5rem;
}
.pokemon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.all-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.thumb-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4.5rem 45px 144px;
  margin: 0.3rem;
  margin-left: 20px;
  border: 1px solid #efefef;
  border-radius: 21px;
  min-width: 160px;
  text-align: center;
  box-shadow: 0 3px 15px #80828b;
  transition: 0.5s ease;
  cursor: pointer;
  height: 167px;
  width: 208px;
}
h3 {
  margin-bottom: 0.2rem;
  margin-top: 20px;
}
.thumb-container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(255, 255, 255, 0.3);
}
.thumb-container img {
  width: 100px;
  height: 100px;
  margin-top: 10px;
}
.thumb-container .img-container {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin-top: 10px;
}
.thumb-container small {
  text-transform: capitalize;
}
.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.thumb-container:hover {
  transform: scale(1.1);
  box-shadow: 5px 5px 15px #515564;
}
@media only screen and (max-width: 600px) {
  .thumb-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4.5rem 45px 144px;
    margin: 0.3rem;
