}
@media only screen and (min-width: 700px) and (max-width: 1300px) {
  .thumb-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4.5px 13px 53px;
    margin: 0.3rem;
    margin-left: -10px;
    border: 1px solid #efefef;
    border-radius: 21px;
    min-width: 160px;
    text-align: center;
    box-shadow: 0 3px 15px #80828b;
