.sidebar_row {
  display: flex;
  align-items: center;
  height: 75px;
  width: 192px;
  padding: 10px 20px;
}
.icon_img {
  color: #606060;
  font-size: 22px !important;
  width: 50px;
  padding-bottom: 22px;
  margin-top: 22px;
}
.sidebarRow_title {
  flex: 1 1;
  margin-left: 15px;
  font-size: 20px;
  font-weight: 600;
  color: white;
  font-family: "Alegreya Sans", sans-serif;
}
.sidebar_row:hover {
  background-color: #2d313d;
  cursor: pointer;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  margin-left: 15px;
  width: 205px;
}
.sidebar_row:hover ::after {
  content: "";
  position: absolute;
  right: 0;

  width: 50px;
  height: 50px;
  background: transparent;
  margin-top: -70px;
  border-radius: 50%;
  box-shadow: 35px 35px 0 10px #2d313d;
  pointer-events: none;
}
.sidebar_row:hover ::before {
  content: "";
  position: absolute;
  right: 0;

  width: 50px;
  height: 50px;
  background: transparent;
  margin-top: 53px;
  border-radius: 50%;
  box-shadow: 35px -25px 0px 10px #2d313d;
  pointer-events: none;
}
@media only screen and (min-height: 1500px) {
  .icon_img {
    color: #606060;
    font-size: 30px !important;
    width: 50px;
    padding-bottom: 50px;
    margin-top: 25px;
    margin-left: 16px;
  }
}

.parent_header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  color: white;
}
.sidebar {
  width: 16%;
  position: fixed;
  top: 71px;
  z-index: 50;
  height: 92vh;
  overflow-y: scroll;
  overflow: hidden;
  left: 0;

  background-color: #3d3e47;

  transition: 0.5s ease;
}
.sidebar.active {
  left: -16%;
}
.mini_sidebar {
  width: 5%;
  position: fixed;
  z-index: 50;
  height: 92vh;
  background-color: #3d3e47;
  left: -100px;
  transition: 0.5s ease;
  top: 71px;
}
@media only screen and (max-width: 600px) {
  .mini_sidebar {
    width: 17%;
    position: fixed;
    z-index: 50;
    height: 92vh;
    background-color: #3d3e47;
    left: -100px;
    transition: 0.5s ease;
    top: 71px;
  }
  .sidebar {
    width: 16%;
    position: fixed;
    top: 71px;
    z-index: 50;
    height: 92vh;
    overflow-y: scroll;
    overflow: hidden;
    left: 0;
    display: none;
    background-color: #3d3e47;

    transition: 0.5s ease;
  }
}
.mini_sidebar.active {
  left: 0;
}
.miniDiv {
  padding: 0px 10px;
  color: #3d3e47;
}
.miniDiv:hover {
  background-color: #2d313d;
  cursor: pointer;
}
@media only screen and (min-device-width: 700px) and (max-device-width: 1300px) {
  .sidebar {
    display: none;
  }
  .mini_sidebar {
    width: 7%;
    position: fixed;
    z-index: 50;
    height: 92vh;
    background-color: #3d3e47;
    left: -100px;
    transition: 0.5s ease;
    top: 71px;
  }
  .mini_sidebar.active {
    left: 0;
  }
  .miniDiv {
    padding: 0px 10px;
    color: #3d3e47;
  }
  .miniDiv:hover {
    background-color: #2d313d;
